/* Home.css */
html {
  background-color: black;
}

.homeContainer {
  padding: 20px;
}

.loadingText {
  color: #0ae719;
  font-size: 24px;
  text-align: center;
}

.movieList {
  padding-left: 30px;
  padding-right: 30px;
  gap: 0px;
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* Creates 3 equal columns */
}

.movieItem {
  display: grid;
  background-color: white;
  padding: 10px;
  border-radius: 10px;
  width: 10cm;
  height: 15cm;
  margin-bottom: 10px;
}

.serviceName {
  font-size: 50px;
  color: greenyellow;
  font-weight: bolder;
  margin-left: 1cm;
}
