.movieTitle {
  font-family: "Arial", sans-serif; /* Choose a nice font */
  font-size: 20px; /* Adjust the font size */
  font-weight: bold; /* Make the text bold */
  color: #333; /* Dark gray color */
  text-align: center; /* Center the text */
  margin-bottom: 10px; /* Add some spacing below the title */
}

.movieTitle a {
  text-decoration: none; /* Remove underline from link */
  color: black; /* Set link color (e.g., Tomato color) */
  transition: color 0.3s ease; /* Smooth color transition on hover */
}

.movieTitle a:hover {
  color: #4e4e4e; /* Darker shade on hover */
}

.movieImg {
  display: block; /* Ensure the image is treated as a block element */
  margin-left: auto; /* Center the image horizontally if needed */
  margin-right: auto; /* Center the image horizontally if needed */
  padding-left: 10px; /* Adjust left padding */
  padding-right: 10px; /* Adjust right padding */
}
